/* eslint-disable */
<template>
	<div class="invites">
		<b-container>
			<div class="header mt-4">
				<div class="h3 font-weight-normal">My Links</div>
			</div>

			<div class="mt-5">
				<div>
					<b-btn :class="`filter_btn ${!!filters.excludeExpired && 'active'} d-flex align-items-center`" @click="filters.excludeExpired = !filters.excludeExpired">
						<b-icon icon="check2" variant="primary" class="mr-2" scale="1.2" v-if="!!filters.excludeExpired"></b-icon>
						<b-iconstack font-scale="1.4" v-else>
							<b-icon stacked icon="slash" variant="primary" scale="1.5"></b-icon>
							<b-icon stacked icon="circle" variant="primary"></b-icon>
						</b-iconstack>
						<div class="mx-2">Exclude expired</div>
					</b-btn>
				</div>

				<div class="invites-body pt-4">
					<b-row cols="1" cols-md="2" cols-lg="3">
						<b-col v-for="invite in invites" :key="invite.id" class="mb-4 h-auto">
							<LinkInvite :invite="invite" />
						</b-col>
					</b-row>
				</div>
				<Paginate :perPage="perPage" :currentPage="currentPage" :total="invitesTotal" />
			</div>
		</b-container>
	</div>
</template>

<script>
	import LinkInvite from "@/components/LinkInvite";
	import Paginate from "@/components/Paginate";

	export default {
		components: { LinkInvite, Paginate },

		data() {
			return {
				filters: { excludeExpired: false },
				perPage: 10,
				currentPage: 1,
			};
		},

		computed: {
			invites() {
				return this.$store.getters.invites;
			},

			invitesTotal() {
				return this.$store.state.invites.total;
			},
		},
	};
</script>
